.header{
    display: flex;
    align-items: center;
    justify-content: space-around;
    position: sticky;
    top: 0;
    gap: 60%;
    width: calc(100% - 20px);
    padding: 10px;
    height: 4%;
    box-shadow: 2px 2px 5px 5px rgba(0, 0,0,0.2);
    background-color: azure;
    border-radius: 10px;
}

.title{
    display: flex;
    flex-direction: row;
    align-items: center;
    gap:  10px;
    width: 20%;
    font-size: 20px;
    font-style: italic;
    font-family: sans-serif;
}

.logo{
    width: 50px;
    height: 50px;
    border-radius: 50%;
    padding: 20px;
    color: #fff;
}

.nav_bar{
    display: flex;
    justify-content: space-around;
    gap: 20px;
}

.nav_bar a{
    text-decoration: none;
    color: #000;
    font-size: 15px;
    font-weight: 500;
}
