.project{
    display: flex;
    flex-direction: column;
    gap: 100px;
    width: 100%;
    height: 190vh;
}

.project_wrap{
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.project_wrap img{
    width: 20%;
    height: 30vh;
    border-radius: 10px;
}

.project_description{
    max-width: 450px;
    font-family: "Jost", sans-serif;
    font-family: "Young Serif", serif;
}

h3{
    margin-top: 0;
}