.container{
    display: flex;
    flex-direction: column;
}


.about_wrap {
  width: 100%;
  display: flex;
  justify-content: center;
  gap: 20px;
}

.job_title {
  color: rgb(59, 55, 55);
  max-width: 25%;
  display: flex;
  font-size: 50px;
  font-family: "Jost", sans-serif;
  font-family: "Young Serif", serif;
}

.about {
  color: rgb(59, 55, 55);
  margin-top: 12.5%;
  max-width: 25%;
  font-family: "Jost", sans-serif;
  font-family: "Young Serif", serif;
}

.social{
    display: flex;
    width: 100%;
    height: 20vh;
    gap: 20px;
    justify-content: center;
    align-items: center;

}

.social span{
    display: flex;
    gap: 10px;
    justify-content: flex-start;
}

.icon{
    text-decoration: none;
}

@media (max-width:748px) {
    .about_wrap{
        flex-direction: column;
        align-items: center;
    }

    .job_title{
       max-width: none;
       margin-left: 20%;
    }

    .about {
        max-width: none;
        margin-left: 20%;
    }
}
